import React, { FC, ReactNode } from 'react';
import {
  QuoteIconsContainer,
  StyledTestimonialCard,
  TestimonialAuthor,
  TestimonialCardContent,
  TestimonialCardLogoContainer,
} from './styled';
import SingleQuoteIcon from '../Icons/SingleQuoteIcon';
import { Text } from '../Text';

export type TestimonialItem = {
  companyLogo: ReactNode;
  content: ReactNode | string;
  authorName: string;
  authorPosition?: string;
  colored?: boolean;
};

const TestimonialCard: FC<TestimonialItem> = ({
  companyLogo,
  content,
  colored,
  authorName,
  authorPosition,
}) => {
  return (
    <StyledTestimonialCard colored={colored}>
      <TestimonialCardLogoContainer>
        {companyLogo}
        <QuoteIconsContainer>
          <SingleQuoteIcon />
          <SingleQuoteIcon />
        </QuoteIconsContainer>
      </TestimonialCardLogoContainer>
      <TestimonialCardContent>
        {typeof content === 'string' ? (
          <Text.Regular.Normal.md>{content}</Text.Regular.Normal.md>
        ) : (
          content
        )}
        {authorName && (
          <TestimonialAuthor>
            <Text.Regular.Bold.md>{authorName}</Text.Regular.Bold.md>,{' '}
            <Text.Regular.Normal.md>{authorPosition}</Text.Regular.Normal.md>
          </TestimonialAuthor>
        )}
      </TestimonialCardContent>
    </StyledTestimonialCard>
  );
};

export default TestimonialCard;
