import React, { FC, ReactNode } from 'react';
import Slider from 'react-slick';
import useWindowSize, { isDesktop } from '../../../hooks/useWindowSize';
import TestimonialCard, { TestimonialItem } from '../TestimonialCard';
import { StyledTestimonials, TestimonialItems, TestimonialsContainer } from './styled';

const TestimonialsSlider: FC<{ testimonials: TestimonialItem[] }> = ({ testimonials }) => {
  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: isDesktopScreen ? 3 : 1,
    slidesToScroll: isDesktopScreen ? 3 : 1,
    autoplay: false,
    arrows: false,
  };

  return (
    <StyledTestimonials>
      <TestimonialsContainer>
        <TestimonialItems>
          <Slider {...settings}>
            {testimonials.map((testimonial) => (
              <TestimonialCard
                key={testimonial.authorName}
                companyLogo={testimonial.companyLogo}
                content={testimonial.content}
                authorName={testimonial.authorName}
                authorPosition={testimonial.authorPosition}
              />
            ))}
          </Slider>
        </TestimonialItems>
      </TestimonialsContainer>
    </StyledTestimonials>
  );
};

export default TestimonialsSlider;
