import { StyledVideoCard, VideoDescription, VideoPreviewImage } from './styled';
import { Text } from '../Text';
import React, { FC, PropsWithChildren, useState } from 'react';
import { Modal } from '../Modal';
import { VideoContainer } from '../styled';
import CustomLink, { LinkArrowType } from '../Link';
import { createPortal } from 'react-dom';
import { colors } from '../../../utils/colors';

type Props = PropsWithChildren<{
  videoExternalLink: string;
  videEmbeddedLink: string;
  linkTitle: string;
  videoDescription?: string;
}>;

export const VideoCard: FC<Props> = ({
  children,
  videoDescription,
  videEmbeddedLink,
  videoExternalLink,
  linkTitle,
}) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <StyledVideoCard>
        <VideoPreviewImage onClick={() => setIsOpen(true)}>{children}</VideoPreviewImage>
        {videoDescription && (
          <VideoDescription>
            <Text.Regular.Normal.md>{videoDescription}</Text.Regular.Normal.md>
          </VideoDescription>
        )}
        <CustomLink
          arrow
          address={videoExternalLink}
          external
          arrowType={LinkArrowType.ShortArrow}
          color={colors.secondary.lightRed}
        >
          {linkTitle}
        </CustomLink>
      </StyledVideoCard>
      {isOpen &&
        createPortal(
          <Modal onClose={() => setIsOpen(false)} showCloseButton>
            <VideoContainer>
              <iframe
                src={videEmbeddedLink}
                title="YouTube video player"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowFullScreen
              ></iframe>
            </VideoContainer>
          </Modal>,
          document.body,
        )}
    </>
  );
};
