export const CustomerStoryCard: FC<{ customerStoryCard: CustomerStoryCard }> = ({
  customerStoryCard,
}) => {
  const { customerLogo, title, linkAddress, benefits, date } = customerStoryCard;
  return (
    <StyledCustomerCard>
      <div>
        <CustomerLogoContainer>{customerLogo}</CustomerLogoContainer>
        <CustomerStoryCardHeadline>
          <Text.Regular.ExtraBold.md>{title}</Text.Regular.ExtraBold.md>
          <Text.Regular.Bold.sm color={colors.primary.gray500}>
            {date}
          </Text.Regular.Bold.sm>
        </CustomerStoryCardHeadline>
        <CustomerStoryCardBenefits>
          {benefits.map((benefit, index) => (
            <CustomerStoryCardBenefitItem key={benefit}>
              <div>
                <RightPlaneArrow id={index.toString()} />
              </div>
              <Text.Regular.Bold.md>{benefit}</Text.Regular.Bold.md>
            </CustomerStoryCardBenefitItem>
          ))}
        </CustomerStoryCardBenefits>
      </div>
      <CustomLink
        address={linkAddress}
        color={colors.secondary.lightRed}
        arrow
        arrowType={LinkArrowType.ShortArrow}
      >
        Read more
      </CustomLink>
    </StyledCustomerCard>
  );
};

export const CustomerStoriesList = () => {
  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: isDesktopScreen ? 2 : 1,
    slidesToScroll: isDesktopScreen ? 2 : 1,
    autoplay: false,
    arrows: false,
    dots: true,
    rows: 1,
  };

  return (
    <CustomerListContainer>
      <Slider {...settings}>
        {customerStoriesCards.map((customerStoryCard) => (
          <CustomerStoriesListSlide key={customerStoryCard.linkAddress}>
            <CustomerStoryCard customerStoryCard={customerStoryCard} />
          </CustomerStoriesListSlide>
        ))}
      </Slider>
    </CustomerListContainer>
  );
};
